<template>
  <div class="login-container">
    <header class="login-header   ">
      <div>
        <div class="pt-2 ">
          <div class="login-img ">
            <img src="../../assets/img/login2.png" alt="">
          </div>
          <div class="text-center text-white pt-1">———— 蜗视商家后台 ————</div>
        </div>
      </div>
    </header>
    <!-- 表单 -->
    <div class="login-wrap ">
      <div class="login_img_1" />
      <div class="login d-flex ">
        <div class="login_body ">
          <div class="pl-4 mt-5">
            <div class="login_ha"><h1>HALLO ;)</h1></div>
            <div class="fs-lg">欢迎使用蜗视商家后台</div>
          </div>
          <div class="login_img" />
        </div>
        <div class="login_form p-2 mt-4">
          <el-form
            v-if="selectLoginTitle === 0"
            ref="loginForm"
            :model="loginForm"
            :rules="loginRules"
            label-width="80px"
          >
            <el-form-item>
              <div class="login_title_group">
                <a class="login_title" :class="selectLoginTitle === 0 ? 'login_title_action' : ''" href="javascript:void(0);" @click="selectLoginTitle = 0">密码登录</a>
                <a class="login_title" :class="selectLoginTitle === 1 ? 'login_title_action' : ''" href="javascript:void(0);" @click="selectLoginTitle = 1">短信登录</a>
              </div>
            </el-form-item>
            <el-form-item prop="username">
              <el-input
                v-model="loginForm.username"
                prefix-icon="el-icon-user-solid"
                placeholder="请输入账户名/手机号"
                clearable
              />
            </el-form-item>
            <el-form-item prop="password">
              <el-input
                v-model="loginForm.password"
                prefix-icon="el-icon-warning"
                show-password
                placeholder="请输入密码"
                clearable
                @keyup.enter.native="handleLogin('loginForm')"
              />
            </el-form-item>
            <el-form-item>
              <el-button
                class="btn "
                round
                @click.native.prevent="handleLogin('loginForm')"
              >立即登录</el-button>
            </el-form-item>
          </el-form>

          <el-form
            v-if="selectLoginTitle === 1"
            ref="SMSloginForm"
            :model="SMSloginForm"
            :rules="SMSloginRules"
            label-width="80px"
          >
            <el-form-item>
              <div class="login_title_group">
                <a class="login_title" :class="selectLoginTitle === 0 ? 'login_title_action' : ''" href="javascript:void(0);" @click="selectLoginTitle = 0">密码登录</a>
                <a class="login_title" :class="selectLoginTitle === 1 ? 'login_title_action' : ''" href="javascript:void(0);" @click="selectLoginTitle = 1">短信登录</a>
              </div>
            </el-form-item>
            <el-form-item prop="phoneNum">
              <el-input
                v-model="SMSloginForm.phoneNum"
                prefix-icon="el-icon-user-solid"
                placeholder="请输入手机号"
                clearable
              />
            </el-form-item>
            <el-form-item prop="verificationCode">
              <el-input v-model="SMSloginForm.verificationCode" placeholder="请输入验证码">
                <template slot="append">
                  <el-button v-if="countdown" style="width: 120px; cursor: not-allowed">{{ countdown }}秒后重发</el-button>
                  <el-button v-else style="width: 120px" @click="getVerificationCode">获取验证码</el-button>
                </template>
              </el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                class="btn "
                round
                @click.native.prevent="onSMSlogin"
              >立即登录</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  data() {
    return {
      loginForm: {
        username: '',
        password: '',
      },
      SMSloginForm: {
        verificationCode: '', // 验证码
        phoneNum: '', // 手机号
      },
      loginRules: {
        username: [{ required: true, message: '请输入账号', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
      },
      SMSloginRules: {
        phoneNum: [{ required: true, message: '请输入手机号', trigger: 'blur' }],
        verificationCode: [{ required: true, message: '请输入验证码', trigger: 'blur' }],
      },
      userId: '',
      selectLoginTitle: 0,
      countdown: 0,
    }
  },
  methods: {
    ...mapActions(['fetchGetMIMCToken']),

    handleLogin() {
      this.$refs.loginForm.validate(async valid => {
        if (!valid) return
        await this.$http.post(this.$api.adminLogin, this.loginForm).then(async res => {
          this.userId = res.data.data.userId
          localStorage.token = res.headers.authorization
          localStorage.shopId = res.data.data.shopId
          localStorage.userId = res.data.data.userId
          localStorage.nickName = res.data.data.userName
          localStorage.userImage = res.data.data.userImage || ''
          localStorage.userArea = res.data.data.userArea
          localStorage.roleCode = res.data.data.roleCode
          if (res.data.code === 200) {
            await this.fetchGetMIMCToken(res.data.data.userId)
            this.$router.push('/main')
            this.$notify({
              title: '成功',
              message: '登录成功',
              type: 'success',
              duration: 2500,
            })
          } else {
            this.$notify.error(res.data.msg)
          }
        })
      })
    },
    onSMSlogin () {
      this.$refs.SMSloginForm.validate(async valid => {
        if (!valid) return
        const data = {
          username: this.SMSloginForm.phoneNum,
          verificationCode: this.SMSloginForm.verificationCode,
          isVerificationCodeLogin: true
        }
        await this.$http.post(this.$api.adminLogin, data).then(async res => {
          this.userId = res.data.data.userId
          localStorage.token = res.headers.authorization
          localStorage.shopId = res.data.data.shopId
          localStorage.userId = res.data.data.userId
          localStorage.nickName = res.data.data.userName
          localStorage.userImage = res.data.data.userImage || ''
          localStorage.userArea = res.data.data.userArea
          localStorage.roleCode = res.data.data.roleCode
          if (res.data.code === 200) {
            await this.fetchGetMIMCToken(res.data.data.userId)
            this.$router.push('/main')
            this.$notify({
              title: '成功',
              message: '登录成功',
              type: 'success',
              duration: 2500,
            })
          } else {
            this.$notify.error(res.data.msg)
          }
        })
      })
    },
    // 获取验证码
    async getVerificationCode () {
      console.log(this.SMSloginForm.phoneNum)
      if (!(/^1[3456789]\d{9}$/.test(this.SMSloginForm.phoneNum))) {
        return this.$message.error('手机号码有误，请重填')
      }
      const data = {
        phoneNum: this.SMSloginForm.phoneNum,
        type: 3,
      }
      const res = await this.$logisticsHttp.post(this.$api.smscode, data)
      if (!res.data.success) return this.$message.error(res.data.msg)
      this.countdown = 60
      const interval = setInterval(() => {
        this.countdown--
        if (this.countdown === 0) {
          clearInterval(interval)
        }
      }, 1000)
    },
  }
}
</script>

<style lang="scss" scoped>
@import url('../../assets/css/login.scss');
  .login_title_group{
    display: flex;
    flex-direction: row;
  }
  .login_title{
    height: 18px;
    line-height: 5px;
    font-size: 16px;
    color: #3c3c3c;
    margin: 9px 10px 0 0;
    font-weight: 700;
  }
  .login_title_action{
    border-bottom: 2px solid #000;
  }
</style>
